.wraper{display:flex;
    height:10vh;
    justify-content:space-between;
   
}

.left_side{
    flex:1;
    align-items: center;
    display: flex;
    gap:2rem;
}
.left_name{font-size: 2.8rem;
font-weight: bold;}

.right_side{
    display: flex;
    flex:1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    font-size: 1.4rem;

}



.right_ul{ flex: 10;
}
.right_ul>ul{display: flex;
gap:4rem;
margin-right: 4rem;}

.right_ul>ul:hover{
    cursor:pointer;
   
}
.right_ul>ul>li:hover{

    color:var(--yellow)
}

.left_name:hover{
    cursor:pointer;
    
    color:var(--yellow)
}


