.intro{display: flex;
    height: 77vh;
    margin-top: 6rem;
}
    
    
    .intro_left{
        display: flex;
        position: relative;
  flex:1;
      font-size: 1.6rem;
      flex-direction: column;
      gap: 2rem;
    
    }


    .intro_name{
        display: flex;
        flex-direction: column;
    }

    .intro_name>:nth-child(1){

        color: var(--black);
        font-weight: bold;
        font-size: 3rem;
    }
    .intro_name>:nth-child(2){
        color: var(--orange);
        font-weight: bold;
        font-size: 3rem;
    }

    .intro_name>:nth-child(3){
        color: var(--grey);
        font-size: 14px;
        font-weight: 100;
        margin-top: 10px;
     
      
    }



    .intro_hire{
       width:10rem ;
       height: 4rem;
    }

  
    .intro_icons{

        margin-top: 3rem;

        display: flex;

        gap: 0rem;
    }

    .intro_icons>*{
        transform: scale(0.5);


    }

    .intro_icons>*{

        cursor: pointer;
    }

    .intro_right{
        flex: 1;
        position: relative;
       

        
    }






    .my_pic{
        position: absolute;
        border-radius: 50%;
    }

